/*RESET*/
html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,ins,kbd,q,samp,small,strong,sub,sup,var,a,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;outline:0 none;background:transparent;font-size:100%;vertical-align:baseline;-moz-osx-font-smoothing:grayscale;text-rendering:optimizeLegibility;}
article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section{display:block;}
img,a img,:link img,:visited img{border:0;outline:none;}
table{border-spacing:0;border-collapse:collapse;}
tr{vertical-align:middle;}
th,td{vertical-align:inherit;}
blockquote,q{quotes:none;}
blockquote:before,blockquote:after,q:before,q:after{content:"";content:none;}
ins{color:#000000;background:#999999;text-decoration:none;}
mark{display:inline-block;padding:0 .5em;color:#000000;background-color:#d1d6d7;font-weight:400;font-style:normal;line-height:1.5;vertical-align:middle;}
del{text-decoration:line-through;}
abbr[title],dfn[title]{border:1px dotted #999999;cursor:help;}
hr{display:block;height:1px;margin:1em 0;padding:0;border:0;background:#d1d6d7;}
button,input,select,textarea{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box;margin:0;padding:0;outline:0 none;vertical-align:middle;-webkit-appearance:none;-moz-appearance:none;appearance:none;}
button,input{overflow:visible;width:auto;}
button::-moz-focus-inner,input::-moz-focus-inner,select::-moz-focus-inner,textarea::-moz-focus-inner{padding:0;border:0;}
textarea{overflow:auto;vertical-align:top;resize:none;}
input[type="button"],input[type="submit"],input[type="reset"],button{cursor:pointer;}
input[type="checkbox"]{-webkit-appearance:checkbox;-moz-appearance:checkbox;appearance:checkbox;}
input[type="radio"]{-webkit-appearance:radio;-moz-appearance:radio;appearance:radio;}
::-webkit-input-placeholder{color:#999999;}
:-moz-placeholder{color:#999999;}
::-moz-placeholder{color:#999999;}
:-ms-input-placeholder{color:#999999;}
pre,code,kbd,samp{font-family:monospace,sans-serif;}
html{overflow-y:scroll;}
small,.small,.b{font-size:.875em;}
big,.big{font-size:1.25em;}
b,strong{font-weight:700;}
i,em{font-style:italic;}
sub{font-size:smaller;line-height:.5;vertical-align:sub;}
sup{font-size:smaller;line-height:.5;vertical-align:super;}
pre{padding:2em;white-space:pre;white-space:pre-wrap;word-wrap:break-word;}
img{max-width:100%;}
label{cursor:pointer;}
/*RESET END*/